
const listeners={

};

const subscribe = (eventName,handler) => {
  if (!listeners[eventName]){
    listeners[eventName] = [];
  }
  let events = listeners[eventName];
  events.push(handler);
};

const notify = (eventName,data) => {
  let events = listeners[eventName];
  if (events){
    events.forEach(handler => {

      handler(data);
    });
  }
};

export {subscribe,notify};



